export default {
	data() {
		return {
			article:{},
		}
	},
	methods: {
		getArticle(id) {
			var that = this;
			that.baseGet(
				that.U({ c: 'article_api', a: `visit`,q: {id: id} }),
				function(res) {
					that.article=res.data;
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
	},
	onLoad(e) {
		this.getArticle(e.id)
	}
}
